import React, { useEffect, useState, PureComponent } from "react";
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { Redirect } from "react-router-dom";
import Paper from "../../components/whiteCard/whiteCard";
import DonutChart from "react-donut-chart";
import url from "../../constants/url";
import * as requests from "../../api/requests";
import Axios from "axios";
import notificationsIcon from "../../assets/icons/notifications.svg";
import projectsIcon from "../../assets/icons/machinery.svg";
import installationIcon from "../../assets/icons/installation.svg";
import ContainerHeader from "../../components/containerHeader/ContainerHeader";

const Home = () => {
  const [projects, setProjects] = useState(null);
  const [notifications, setNotifications] = useState(null);
  const [installationsData, setInstallations] = useState([]);
  const data = [
    {
      name: "Page A",
      uv: 4000,
      pv: 2400,
      amt: 2400,
    },
    {
      name: "Page B",
      uv: 3000,
      pv: 1398,
      amt: 2210,
    },
    {
      name: "Page C",
      uv: 2000,
      pv: 9800,
      amt: 2290,
    },
    {
      name: "Page D",
      uv: 2780,
      pv: 3908,
      amt: 2000,
    },
    {
      name: "Page E",
      uv: 1890,
      pv: 4800,
      amt: 2181,
    },
    {
      name: "Page F",
      uv: 2390,
      pv: 3800,
      amt: 2500,
    },
    {
      name: "Page G",
      uv: 3490,
      pv: 4300,
      amt: 2100,
    },
  ];
  let userAreaList = [];
  let zonesArr = [];

  useEffect(() => {
    let areaList = [];
    Axios.post(url + "/notifications/getNotificationByArea", {
      area: areaList,
    })
      .then((data) => {
        let jobs = 0;
        data.data.map((notif) => {
          if (notif.isJob) jobs++;
        });
        let total = data.data.length;
        setNotifications({ jobs, total });
      })
      .catch((e) => {});
    requests
      .post({
        url: "/projects/getUserProjects",
        body: { userId: localStorage.getItem("Id") },
      })
      .then((data) => {
        // console.log(data);
        let installations = [];
        data.map((project) => {
          let flag = false;
          installations.map((installation) => {
            if (installation.name == project.installation.name) {
              flag = true;
              if (project.state === "Ongoing") {
                installation.Ongoing = installation.Ongoing + 1;
              } else if (project.state === "Halt") {
                installation.Halt = installation.Halt + 1;
              } else if (project.state === "Removed") {
                installation.Removed = installation.Removed + 1;
              } else if (project.state === "Completed") {
                installation.Completed = installation.Completed + 1;
              }
            }
          });
          if (!flag) {
            installations.push({
              name: project.installation.name,
              Ongoing: project.state === "Ongoing" ? 1 : 0,
              Halt: project.state === "Halt" ? 1 : 0,
              Removed: project.state === "Removed" ? 1 : 0,
              Completed: project.state === "Completed" ? 1 : 0,
            });
          }
        });
        setInstallations(installations);
        let halt = 0;
        let ongoing = 0;
        let completed = 0;
        let removed = 0;
        data.map((project) => {
          if (project.state == "Completed") completed++;
          if (project.state == "Halt") halt++;
          if (project.state == "Ongoing") ongoing++;
          if (project.state == "Removed") removed++;
        });
        setProjects({
          completed,
          halt,
          removed,
          ongoing,
        });
      })
      .catch((e) => {});
  }, []);
  return (
    <div>
      <ContainerHeader formName="Dashboard" />
      <Paper>
        <div id="dashboard" style={styles.structure}>
          <div style={{ padding: "20px", ...styles.structureIn }}>
            <div>
              <p>Name:</p>
              <p>Mobile Number:</p>
              <p>Email:</p>
              <p>Status:</p>
              <p>User Code:</p>
              <p>Designation:</p>
              <p>Areas:</p>
              <p>Zones:</p>
            </div>
            <div>
              <p>{localStorage.getItem("Name")}</p>
              <p>{localStorage.getItem("mobile")}</p>
              <p>{localStorage.getItem("Email")}</p>
              <p>{localStorage.getItem("status")}</p>
              <p>{localStorage.getItem("userCode")}</p>
              <p>{localStorage.getItem("Role")}</p>
              <p>
                {userAreaList.map((area) => (
                  <>{area + ", "}</>
                ))}
              </p>
              <p>
                {zonesArr.map((zone) => (
                  <>{zone + ", "}</>
                ))}
              </p>
            </div>
          </div>
          {window.innerWidth > 800 ? (
            <div style={{ padding: "20px" }}>
              <img
                src={url + "/userPics/" + localStorage.getItem("photo")}
                alt="User Photo"
                width="200px"
              />
            </div>
          ) : null}
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: window.innerWidth > 800 ? "row" : "column",
            justifyContent: "space-evenly",
            marginTop: "40px",
          }}
        >
          {projects && window.innerWidth > 800 ? (
            <div>
              <DonutChart
                height={300}
                width={400}
                legend={true}
                colors={["#f1c40f", "#2ecc71", "#1abc9c", "#e74c3c"]}
                data={[
                  {
                    label: "Ongoing",
                    value: projects.ongoing,
                  },
                  {
                    label: "Halt",
                    value: projects.halt,
                  },
                  {
                    label: "Removed",
                    value: projects.removed,
                  },
                  {
                    label: "Completed",
                    value: projects.completed,
                  },
                ]}
              />
            </div>
          ) : null}
          {notifications && window.innerWidth > 800 ? (
            <div>
              <DonutChart
                height={300}
                width={410}
                legend={true}
                colors={["#e74c3c", "#2ecc71"]}
                data={[
                  {
                    label: "Jobs",
                    value: notifications.jobs,
                  },
                  {
                    label: "Unassigned",
                    value: notifications.total - notifications.jobs,
                    isEmpty: true,
                  },
                ]}
              />
            </div>
          ) : null}
        </div>
        <div
          style={{
            display: "grid",
            gridTemplateColumns: window.innerWidth > 800 ? "50% 50%" : "100%",
          }}
        >
          {window.innerWidth > 800 ? (
            <div
              style={{
                backgroundColor: "white",
                borderLeft: "3px solid #1abc9c",
                borderTopLeftRadius: "3px",
                borderBottomLeftRadius: "3px",
                padding: "20px",
                boxShadow: "0px 0px 2px rgba(30,30,30,0.3)",
                width: "500px",
                height: "350px",
              }}
            >
              <BarChart
                width={500}
                height={300}
                data={installationsData}
                margin={{
                  top: 5,
                  right: 30,
                  left: 20,
                  bottom: 5,
                }}
              >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Bar dataKey="Ongoing" fill="#8884d8" />
                <Bar dataKey="Halt" fill="#82ca9d" />
                <Bar dataKey="Completed" fill="rgb(10 98 64)" />
                <Bar dataKey="Removed" fill="Red" />
              </BarChart>
            </div>
          ) : null}
          <div
            style={{
              padding: window.innerWidth < 800 ? "0px" : "30px",
              display: "grid",
              gridTemplateRows: "1fr 1fr 1fr",
            }}
          >
            <div style={styles.card}>
              <div style={{ backgroundColor: "#148f77", position: "relative" }}>
                <img
                  style={styles.centerIcon}
                  src={notificationsIcon}
                  alt="Notifications"
                  width="50px"
                />
              </div>
              <div style={{ backgroundColor: "#1abc9c" }}>
                <h1 style={styles.cardNumber}>
                  {notifications ? notifications.total : 0}
                </h1>
                <p style={styles.cardName}>Notifications</p>
              </div>
            </div>
            <div style={styles.card}>
              <div style={{ backgroundColor: "#25a25a", position: "relative" }}>
                <img
                  style={styles.centerIcon}
                  src={projectsIcon}
                  alt="Projects"
                  width="50px"
                />
              </div>
              <div style={{ backgroundColor: "#2ecc71" }}>
                <h1 style={styles.cardNumber}>
                  {projects
                    ? projects.completed +
                      projects.halt +
                      projects.ongoing +
                      projects.removed
                    : 0}
                </h1>
                <p style={styles.cardName}>Projects</p>
              </div>
            </div>
            <div style={styles.card}>
              <div
                style={{
                  backgroundColor: "rgb(10 98 64)",
                  position: "relative",
                }}
              >
                <img
                  style={styles.centerIcon}
                  src={installationIcon}
                  alt="Installations"
                  width="50px"
                />
              </div>
              <div style={{ backgroundColor: "rgb(43 121 62)" }}>
                <h1 style={styles.cardNumber}>
                  {installationsData ? installationsData.length : 0}
                </h1>
                <p style={styles.cardName}>installations</p>
              </div>
            </div>
          </div>
        </div>
      </Paper>
    </div>
  );
};

const styles = {
  card: {
    backgroundColor: "white",
    boxShadow: "0px 0px 2px rgba(30,30,30,0.3)",
    height: "90px",
    width: "350px",
    display: "grid",
    gridTemplateColumns: "2fr 5fr",
    borderRadius: "3px",
    overflow: "hidden",
  },
  centerIcon: {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%,-50%)",
  },
  structure: {
    display: "grid",
    gridTemplateColumns: window.innerWidth > 800 ? "2fr 1fr" : "1fr",
  },
  structureIn: {
    display: "grid",
    gridTemplateColumns: window.innerWidth > 800 ? "2fr 1fr" : "1fr 1fr",
  },
  cardNumber: {
    color: "white",
    marginBottom: 0,
    marginTop: "10px",
    marginLeft: "20px",
  },
  cardName: {
    marginTop: 0,
    color: "white",
    marginLeft: "20px",
  },
};
export default Home;
