import React, { useState } from "react";
import "./login.css";
import * as requests from "../../api/requests";
import Modal from "../../components/ui/modal/Modal";
import Loader from "../../components/spinner/Spinner";

const Login = (props) => {
  const [user, setUser] = React.useState({ email: "", password: "" });
  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  const login = () => {
    setError(false);
    setLoading(true);
    setShow(true);
    requests
      .post({
        url: "/users/login",
        body: { email: user.email, password: user.password },
      })
      .then((res) => {
        localStorage.setItem("Role", res.designation);
        localStorage.setItem("Id", res._id);
        localStorage.setItem("Name", res.name);
        localStorage.setItem("Email", res.email);
        localStorage.setItem("userCode", res.userCode);
        localStorage.setItem("status", res.status);
        localStorage.setItem("mobile", res.mobileNo);
        localStorage.setItem("photo", res.photo);
        localStorage.setItem("areaAndZone", JSON.stringify(res.areaAndZone));
;        window.location.pathname = "/dashboard";
        modalClose();
        setLoading(false);
      })
      .catch((e) => {
        setError(true);
        setLoading(false);
      });
  };

  const modalClose = () => {
    setShow(false);
  };

  const emailChange = (e) => {
    const temp = e.target.value;
    setUser({ ...user, email: temp });
  };
  const passChange = (e) => {
    const temp = e.target.value;
    setUser({ ...user, password: temp });
  };

  const passwordKeyPressHandler = (event) => {
    if (event.key === "Enter") {
      login();
    }
  };

  return (
    <div
      className="login-container"
      style={{
        backgroundColor: '#202950'
      }}
    >
      {loading || error ? (
        <Modal show={show} noHandler={modalClose}>
          {error ? <p>Something went wrong!</p> : <Loader></Loader>}
        </Modal>
      ) : null}
      {props.loggedIn ? (window.location.pathname = "/dashboard") : null}
      <div>
        <div style={{display: 'grid', placeItems: 'center', color: 'white'}}>
          <h1 style={{color: 'white', fontFamily:'Nunito', fontSize: '40px'}}>The_Invincible's Hack</h1>
        </div>
        <center>
          <div className="login-card">
            <div className="login-input-container" style={{display: 'flex', placeItems: 'center', height: '100%'}}>
              <span>Email or User code</span>
              <input
                className="login-ipt"
                placeholder="Enter Email or User code"
                type="text"
                name="salarycode"
                value={user.email}
                onChange={emailChange}
              />
              <span>Password</span>
              <input
                className="login-ipt"
                placeholder="Password"
                type="password"
                name="password"
                value={user.password}
                onChange={passChange}
                onKeyPress={passwordKeyPressHandler}
              />
              <button className="login-signin" onClick={login}>
                SIGN IN
              </button>
            </div>
          </div>
        </center>
      </div>
    </div>
  );
};

export default Login;
